<template>
    <div class="user-message-store">
        <div class="com-title-bar">
            <div class="box">
                <div class="back" @click="$routerBack"></div>
                <div class="title">查看消息</div>
                <div class="action">
                    <div class="button"></div>
                </div>
            </div>
        </div>
        <div class="data">
            <div class="title">{{ message.title }}</div>
            <div class="date van-hairline--bottom">{{ message.create_time }}</div>
            <div class="body">{{ message.body }}</div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                message: {}
            }
        },
        mounted() {
            this.initData();
        },
        methods: {
            initData() {
                let url = '';
                let type = this.$route.query.type;
                switch (type) {
                    case 'system':
                        url = '/api/app/system/notice/store';
                        break;
                    case 'user':
                        url = '/api/app/user/message/store';
                        break;
                    default:
                        url = '/api/app/system/notice/store'
                }
                this.$axios.get(url, {
                    uuid: this.$route.query.uuid
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.message = result;
                        }
                    })
            }
        }
    }
</script>

